import { useAuth } from '@/hooks/useAuth';
import Link from 'next/link';

export default function Header() {
  const { user, isAuthenticated, logout } = useAuth();

  return (
    <header className="fixed w-full bg-white shadow-sm">
      <div className="container mx-auto px-6 py-4">
        <nav className="flex justify-between items-center">
          <div className="flex items-center">
            <Link href="/" 
               className="text-gray-600 hover:text-blue-600 font-medium px-4 py-2 rounded-lg hover:bg-blue-50 transition-colors">
              Home
            </Link>
            <div className="h-6 w-[2px] bg-gray-300 mx-2"></div>
            <Link href="/ai-search" 
               className="text-gray-600 hover:text-blue-600 font-medium px-4 py-2 rounded-lg hover:bg-blue-50 transition-colors">
              AI Search
            </Link>
            <div className="h-6 w-[2px] bg-gray-300 mx-2"></div>
            <Link href="/ai-document" 
               className="text-gray-600 hover:text-blue-600 font-medium px-4 py-2 rounded-lg hover:bg-blue-50 transition-colors">
              AI Document Automation
            </Link>
          </div>

          <div className="flex items-center space-x-4">
            {isAuthenticated ? (
              <div className="flex items-center space-x-4">
                <span className="text-gray-700">{user?.name}</span>
                <button
                  onClick={logout}
                  className="text-gray-600 hover:text-blue-600 font-medium px-4 py-2 rounded-lg hover:bg-blue-50 transition-colors"
                >
                  Î°úÍ∑∏ÏïÑÏõÉ
                </button>
              </div>
            ) : (
              <>
                <Link
                  href="/login"
                  className="text-gray-600 hover:text-blue-600 font-medium px-4 py-2 rounded-lg hover:bg-blue-50 transition-colors"
                >
                  Î°úÍ∑∏Ïù∏
                </Link>
                <Link
                  href="/signup"
                  className="bg-blue-500 text-white font-medium px-4 py-2 rounded-lg hover:bg-blue-600 transition-colors"
                >
                  ÌöåÏõêÍ∞ÄÏûÖ
                </Link>
              </>
            )}
          </div>
        </nav>
      </div>
    </header>
  );
} 